<template>
    <main class="parallax-parent">
        <div class="hero-banner parallax-container">
            <h1 class="center">Zaključna naloga</h1>
            <h3>Pri predmetu Oblikovanje vizualnih komunikacij</h3>
        </div>
        
        <div class="list-wrapper content-container">
            <div class="divider"></div>

            <ListOfProjects></ListOfProjects>
        </div>

        <footer>
            <p>Nejc Povšič</p>
        </footer>
    </main>
</template>

<script>
    import ListOfProjects from '../components/ListOfProjects';

    export default {
        name: 'LandingPage',
        components: {
            ListOfProjects
        }
    }
</script>

<style>
    main {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    footer {
        margin-top: 64px;
        padding: 32px;
        width: 100%;
        height: 200px;
        background-color: #ccc;
    }
    
    .hero-banner {
        width: 100%;
        background-color: #ccc;

        display: flex;
        
        flex-direction: column;

        justify-content: center;
        align-items: center;
    }
    
    .list-wrapper {
        width: 75%;
        margin-top: -24px;
    }

    .parallax-parent {
        perspective: 1px;
        transform-style: preserve-3d;
        height: 100vh;
        overflow-x: hidden;
        overflow-y: scroll;
    }

    .parallax-container {
        display: flex;
        flex: 1 0 auto;
        position: relative;
        transform: translateZ(-1px) scale(2);
        z-index: -1;
        height: 600px;
        transform-origin: 50% 600px;
    }

    .content-container {
        display: block;
        position: relative;
        background-color: transparent;
        z-index: 1;
        
        max-width: 1100px;
    }
</style>


