<template>
    <ul class="projects-list">
        <li class="projects-list__list-item" v-for="project in projects">
            <div class="projects-list__list-item__content">
                <div class="projects-list__list-item__content__title">
                    <p>{{project.title}}</p>
                </div>

                <div class="projects-list__list-item__content__content-container">
                    <p v-html="project.text"></p>
                </div>
                
                <div class="projects-list__list-item__content__button-container">
                    <button>Button</button>
                </div>
            </div>
        </li>
    </ul>
</template>

<script>
    export default {
        name: 'ListOfProjects',
        data() {
            const projects = [
                {
                    title: 'Logotip skupine',
                    text: 'Pozdrav svima.<br/>Kako ste'
                },
                {
                    title: 'Tipografija',
                    text: ''
                },
                {
                    title: 'Piktogrami',
                    text: ''
                },
                {
                    title: 'Plakat',
                    text: ''
                },
                {
                    title: 'Poster za film',
                    text: ''
                },
                {
                    title: 'Embalaža',
                    text: ''
                },
                {
                    title: 'Knjiga',
                    text: ''
                },
                {
                    title: 'Plakati Miltona Glaserja',
                    text: ''
                },
                {
                    title: 'Kažipot',
                    text: ''
                }
            ];

            return {
                projects
            };
        }
    }
</script>

<style scoped>
    .projects-list {
        display: flex;
        flex-direction: column;

        list-style-type: none;

        padding: 0;
        margin: 0;
    }

    .projects-list:before {
        content: '';
        height: 98%;
        width: 5px;
        background-color: white;
        position: absolute;
        top: 2%;
        left: 50%;

        border: 1px solid;
    }

    .projects-list__list-item {
        display: flex;
    }

    .projects-list__list-item:nth-child(even) {
        justify-content: end;
    }

    .projects-list__list-item__content {
        width: 40%;
        margin: 32px;
    }

    .projects-list__list-item__content__title {
        padding: 16px 32px;
        background-color: #eee;

        border: 1px solid;
    }

    .projects-list__list-item__content__content-container {

    }
    
    .projects-list__list-item__content__button-container {
        display: flex;
        justify-content: start;

        padding: 16px 0;
    }

    .projects-list__list-item:nth-child(even) > .projects-list__list-item__content > .projects-list__list-item__content__button-container {
        justify-content: end;
    }
    
    button {
        background-color: var(--main-bg-color);
        border: 1px solid red;

        padding: 8px 16px;        
    }
</style>


